<template>
  <div class="addArticle">
    <el-row>
      <el-col :span="8">
        <!-- <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="1"
          :on-exceed="handleExceed"
          :file-list="fileList"
          list-type="picture"
          :headers="headers"
        >
          <el-button size="small" type="primary" class="upload">选择主图</el-button>
        </el-upload>-->

        <el-upload
          class="upload-demo"
          drag
          list-type="picture"
          :fileList="fileList"
          :limit="1"
          :action="$baseURL+'/upload/uploadFileMasterGraph'"
          :headers="headers"
          :on-success="uploadSuccess"
          :on-error="uploadError"
          multiple
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将
            <span style="color: red;">主图</span>拖到此处，或
            点击上传
            <p style="color: #005aff;">建议图片比例统一</p>
          </div>
        </el-upload>
      </el-col>

      <el-col :span="16">
        <el-form ref="article" :model="article" label-width="80px">
          <el-form-item label="文章标题">
            <el-input v-model="article.title" placeholder="50字符内" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="文章描述">
            <el-input v-model="article.intro" placeholder="150字符内" maxlength="150"></el-input>
          </el-form-item>
          <el-form-item label="关键字">
            <el-input
              v-model="article.keyword"
              placeholder="3-5个关键字（关键字与关键字之间用英文逗号隔开）"
              maxlength="80"
              class="keyword"
            ></el-input>
            <el-select v-model="article.c_id" placeholder="请选择所属文章类别" class="articleCategory">
              <el-option
                v-for="item in categoryList"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
            <el-switch class="hot"
              style="display: block"
              v-model="value2"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="首页展示"
            ></el-switch>

            <el-button class="subBtn" type="primary" @click="submitArticle">发布</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

    <Markdown :onchange="change" v-bind:initData="initData"></Markdown>
  </div>
</template>

<script>
import Markdown from "../../components/markdown/markdown-editor";
import initData from "../../markData.js";
export default {
  name: "addArticle",
  data() {
    return {
      value2: false,
      fileList: [],
      categoryList: [],
      article: {
        c_id: "", //文章类别id
        title: "",
        keyword: "",
        intro: "",
        picture: "",
        markdown: "",
        hot:"",
        html: "",
        text: ""
        // content: {}
      },
      headers: { token: this.$store.getters.token },

      initData: initData
    };
  },
  components: { Markdown },
  created() {
    this.findByCategoryAll();
  },
  methods: {
    uploadSuccess(response, file, fileList) {
      console.log(response, fileList);
      if (response.ok) {
        this.article.picture = response.data.src;
        this.$message({
          showClose: true,
          message: "上传成功！",
          type: "success"
        });
      } else {
        this.fileList = [];
        console.log("resp失败onse", fileList);
        this.$message({
          showClose: true,
          message: "上传失败！",
          type: "error"
        });
      }

      return false;
    },
    uploadError(error, file, fileList) {
      console.log(error, file, fileList);
      this.$message({
        showClose: true,
        message: "上传失败！",
        type: "error"
      });
      return false;
    },
    findByCategoryAll(pageNum, pageSize) {
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/exampleCategory/findByAll",
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.categoryList = response.data;
          this.article.c_id = this.categoryList[0].id;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    },
    change() {
      console.log("编辑", arguments[0]);
      this.article.markdown = arguments[0].markdown;
      this.article.html = arguments[0].html;
      this.article.text = arguments[0].text;
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    handleSelect(item) {
      console.log(item);
    },
    submitArticle() {
      console.log(this.article);
      if(this.value2){
        this.article.hot="1"
      }else{
        this.article.hot="0"
      }

      for (var key in this.article) {
        if (this.article[key] == "" || this.article[key] == null) {
          this.$message({
            showClose: true,
            message: "请填写全部信息，不能为空！",
            type: "error"
          });
          return;
        }
      }
      this.$axios({
        method: "post",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/example/add",
        data: this.article,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });

      console.log("源码", this.article);
    }
  },
  mounted() {
    // this.headers.token = this.$store.getters.token;
  }
};
</script>

<style >
.upload-demo {
  width: 80%;
  /* margin: 0 auto; */
  position: relative;
}
.el-upload-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 28px;
  left: 10px;
}
.upload {
  background: #fff;
  border: 0;
  color: #409eff;
}
.articleCategory {
  width: 20%;
  margin-left: 0px;
}
.subBtn {
  width: 10%;
  margin: 0 auto;
  float: right;
}
.keyword {
  float: left;
  width: 48%;
}
.hot{
   width: 18%;
  margin: 10px 8px;
  float: left;
}
</style>



